@import url(https://fonts.googleapis.com/css2?family=News+Cycle&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=News+Cycle&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@200&family=Montserrat:wght@200&family=News+Cycle&display=swap);
