*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}a,a:hover,a:visited{text-decoration:none}li{list-style:none}h1,h2,h3{font-family:"Josefin Sans", sans-serif;font-weight:300}p{font-family:"Alata", sans-serif}:root{font-family:"Montserrat",sans-serif}footer{background-color:aliceblue}header{height:10vh;width:100vw;background-color:white}@media screen and (max-width: 768px){header{height:20vh}}header nav{display:flex;justify-content:center;align-items:center;flex-direction:row;width:100vw;height:10vh;background-color:white;padding-top:1rem;padding-bottom:1rem;border-bottom:1px solid #cacaca}@media screen and (max-width: 768px){header nav .navbar{padding-top:1rem}}header nav .navbar #logo-wrapper{width:15rem}header nav .navbar #logo-wrapper .logo img{margin:2rem;z-index:100}header nav .navbar form .area{border:1px solid red;height:25px;width:200px;font-size:12px;padding:10px}header nav .navbar form .area:focus{outline:none}header nav .navbar{width:100vw;height:10vh;background-color:transparent;border-left:30px solid white;border-right:30px solid white}header nav__links ul{display:flex;justify-content:row;align-items:center;flex-direction:none;margin-top:9px}header nav__links li{font-size:100px;font-weight:700;letter-spacing:0.6px;margin-top:2px}header nav__links li:not(:last-child){margin-right:31.5px}header nav__links li:last-child{margin-right:0px}header nav__links li a{color:black;position:relative}@media screen and (max-width: 768px){header nav__links li{display:none}}div .descr{border-bottom:1px solid #cacaca}*{font-family:"Montserrat",sans-serif}

